.featured{
	height: 100vh;

}

.featured--vertical{
	/*//background-color: rgb(0,0,0);*/
	height: inherit;
	width: inherit;
	background: linear-gradient(to top, #111 5%, rgba(0,212,255,0) 90%);
}

.featured--horizontal{
	height: inherit;
	width: inherit;
	background: linear-gradient(90deg, #111 20%, rgba(0,212,255,0) 90%);
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding-left:30px ;
	padding-bottom: 150px;
	padding-top: 70px;
}


.featured--name{
	font-size: 60px;
	font-weight: bold;
}
.featured--info{
	font-size: 18px;
	font-weight: bold;
	margin-top: 15px;
}

.featured--point,.featured--year,.featured--seasons{
	display: inline-block;
	margin-right: 15px;
}

.featured--point{
	color: #46d369;
}

.featured--description{
	margin-top: 15px;
	font-size: 20px;
	font-weight: bold;
	color: #888;
	max-width: 40%;
}

.featured--buttons{
	margin-top: 15px;
	font-size: 20px;
	font-weight: bold;
}

.featured--watch,.featured--add{
	padding: 15px 25px;
	display: inline-block;
	text-decoration: none;
	border-radius: 5px;
	margin-right: 15px;
	transition: all .3s;
}


.featured--buttons .featured--watch{
	color: #000;
	background-color: #fff;
}

.featured--buttons .featured--add{
	color: #fff;
	background-color: #999;
}

.featured--watch:hover, .featured--add:hover{
	opacity: 0.8;

}

.featured--genres{
	margin-top: 15px;
	font-size: 18px;
	color: #999;
}

.featured--created{
	display: flex;
	width: 100%;
	position: relative;
	/*top: 100px;*/
	justify-content: center;
	align-items: center;
	font-size: 20px;
	font-weight: bold;
}

.featured--created-items{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 0px 100px;
}
.featured--created-items > p{
	margin-top: 5px;
}