.movieRow{
	margin-bottom: 50px;
	position: relative;
}

.movieRow h2{
	margin: 0px 0px 10px 35px;
}

.movieRow--listarea{
	overflow-x:hidden ;
	padding-left: 30px;

}

.movieRow--list{
	overflow-y: hidden;
	
}

.movieRow--item{
	display: inline-block;
	cursor: pointer;
	position: relative;
}

.movieRow--item img{
	width: 100%;
	transform: scale(.95);
	transition: all ease 0.3s;
}

.movieRow--item img:hover{
	transform: scale(1);
}

.movieRow--item .movieRow--item--img--net{
	position: absolute;
	width: 25px;
	z-index: 99;
	left: 10px;
	top: 10px;
	transform: scale(.95);

}

.movieRow--right, .movieRow--left{
	position: absolute;
	width: 40px;
	height: 85%;
	background: rgba(0,0,0,0.6);
	z-index: 99;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	cursor: pointer;
	opacity: 0;
	transition: opacity .5s ease;
}

.movieRow--left{
	left: 0;
}

.movieRow--right{
	right: 0;
}

.movieRow:hover .movieRow--right,.movieRow:hover .movieRow--left{
	opacity: 1;
	transition: opacity .5s ease;
}


.movieRow--item--trailer{
	background-color: #111;
	flex-direction: column;
	z-index:100;
	position:absolute;
	top:0px;
	transition:all .5s;
	overflow:hidden;
	border-radius: 10px;
	box-shadow: 2px 2px 10px 2px #111;
}

.movieRow--item--trailer .movieRow--item--trailer--data--button {
	padding: 20px 20px 10px 20px;
	display: flex;
	justify-content: space-between;
}

.trailer--data--button1, .trailer--data--button2,.trailer--data--button3{
	background-color:#222;
	padding:5px;
	border-radius:50%;
	border:2px solid #fff;
	margin-right:10px;
	color: #fff;
	cursor: pointer;
}

.trailer--data--button3{
	margin-right: 0px;
}

.trailer--data--button1:hover,.trailer--data--button2:hover,.trailer--data--button3:hover{
	background-color: #fff;
	color: #000;
}

.movieRow--item--trailer .movieRow--item--trailer--data{
	display: flex;
	width: 100%;
	transition: all .6s;
	padding-left: 10px;
	padding-right: 10px;

}
.movieRow--item--trailer .movieRow--item--trailer--data .trailer--data{
	width: 70%;
	padding: 0px 10px;

}

.movieRow--item--trailer .movieRow--item--trailer--data h2{
	margin: 0;
}

.movieRow--item--trailer .trailer{
	display: flex;

}

.movieRow--item--trailer .trailer p{
	margin-right: 10px;
	font-weight: bold;

}

.movieRow--item--trailer .trailer .trailer--vote{
	color: #46d369;
}

.movieRow--item--trailer .trailer--overview{
	font-weight: bold;
}
.movieRow--item--trailer > div .trailer--margin{
	margin-left: 20px;
}

.movieRow--item--trailer .trailer--extra{
	display: flex;
	flex-direction: column;
	width: 30%;
	color: #aaa;

}

.movieRow--item--trailer .trailer--extra--width{
	width: 100%;
	padding-left: 20px;
	padding-bottom: 30px;
}

.trailer--extra strong{
	color: #fff;
}


.button--active--green{
	background-color: #46d369;
}

.button--active--red{
	background-color: #d34646;
}


 

