header{
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	right: 0;
	height: 70px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 30px;
	background: linear-gradient(180deg,#111,#111,#111111aa,transparent,transparent);
	transition: .5s background ease;
}


header.black{
	background: #141414;
	transition: .5s background ease;


}

.header--logo{
	height: 25px;
}

.header--logo img{
	height: 100%;
}


.header--user{
	height: 35px;

}

.header--user img{
	height: 100%;
	border-radius: 3px;
}
