@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.movieRow{
	margin-bottom: 50px;
	position: relative;
}

.movieRow h2{
	margin: 0px 0px 10px 35px;
}

.movieRow--listarea{
	overflow-x:hidden ;
	padding-left: 30px;

}

.movieRow--list{
	overflow-y: hidden;
	
}

.movieRow--item{
	display: inline-block;
	cursor: pointer;
	position: relative;
}

.movieRow--item img{
	width: 100%;
	-webkit-transform: scale(.95);
	        transform: scale(.95);
	transition: all ease 0.3s;
}

.movieRow--item img:hover{
	-webkit-transform: scale(1);
	        transform: scale(1);
}

.movieRow--item .movieRow--item--img--net{
	position: absolute;
	width: 25px;
	z-index: 99;
	left: 10px;
	top: 10px;
	-webkit-transform: scale(.95);
	        transform: scale(.95);

}

.movieRow--right, .movieRow--left{
	position: absolute;
	width: 40px;
	height: 85%;
	background: rgba(0,0,0,0.6);
	z-index: 99;
	display: flex;
	align-items: center;
	justify-content: center;
	overflow: hidden;
	cursor: pointer;
	opacity: 0;
	transition: opacity .5s ease;
}

.movieRow--left{
	left: 0;
}

.movieRow--right{
	right: 0;
}

.movieRow:hover .movieRow--right,.movieRow:hover .movieRow--left{
	opacity: 1;
	transition: opacity .5s ease;
}


.movieRow--item--trailer{
	background-color: #111;
	flex-direction: column;
	z-index:100;
	position:absolute;
	top:0px;
	transition:all .5s;
	overflow:hidden;
	border-radius: 10px;
	box-shadow: 2px 2px 10px 2px #111;
}

.movieRow--item--trailer .movieRow--item--trailer--data--button {
	padding: 20px 20px 10px 20px;
	display: flex;
	justify-content: space-between;
}

.trailer--data--button1, .trailer--data--button2,.trailer--data--button3{
	background-color:#222;
	padding:5px;
	border-radius:50%;
	border:2px solid #fff;
	margin-right:10px;
	color: #fff;
	cursor: pointer;
}

.trailer--data--button3{
	margin-right: 0px;
}

.trailer--data--button1:hover,.trailer--data--button2:hover,.trailer--data--button3:hover{
	background-color: #fff;
	color: #000;
}

.movieRow--item--trailer .movieRow--item--trailer--data{
	display: flex;
	width: 100%;
	transition: all .6s;
	padding-left: 10px;
	padding-right: 10px;

}
.movieRow--item--trailer .movieRow--item--trailer--data .trailer--data{
	width: 70%;
	padding: 0px 10px;

}

.movieRow--item--trailer .movieRow--item--trailer--data h2{
	margin: 0;
}

.movieRow--item--trailer .trailer{
	display: flex;

}

.movieRow--item--trailer .trailer p{
	margin-right: 10px;
	font-weight: bold;

}

.movieRow--item--trailer .trailer .trailer--vote{
	color: #46d369;
}

.movieRow--item--trailer .trailer--overview{
	font-weight: bold;
}
.movieRow--item--trailer > div .trailer--margin{
	margin-left: 20px;
}

.movieRow--item--trailer .trailer--extra{
	display: flex;
	flex-direction: column;
	width: 30%;
	color: #aaa;

}

.movieRow--item--trailer .trailer--extra--width{
	width: 100%;
	padding-left: 20px;
	padding-bottom: 30px;
}

.trailer--extra strong{
	color: #fff;
}


.button--active--green{
	background-color: #46d369;
}

.button--active--red{
	background-color: #d34646;
}


 


*{
  box-sizing: border-box;
}


body{
  background-color: #111;
  /*background: #000;*/
  color: #fff;
  margin: 0px;
  font-family:'Roboto',sans-serif ;
}

.lists{
  margin-top: -150px;
}

footer{
  margin: 50px 0px;
  text-align: center; 
}

footer span{
  color: #f00;
}

.loading{
  position: fixed;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
}
.loading img{
  width: 100%;
}
.featured{
	height: 100vh;

}

.featured--vertical{
	/*//background-color: rgb(0,0,0);*/
	height: inherit;
	width: inherit;
	background: linear-gradient(to top, #111 5%, rgba(0,212,255,0) 90%);
}

.featured--horizontal{
	height: inherit;
	width: inherit;
	background: linear-gradient(90deg, #111 20%, rgba(0,212,255,0) 90%);
	display: flex;
	flex-direction: column;
	justify-content: center;
	padding-left:30px ;
	padding-bottom: 150px;
	padding-top: 70px;
}


.featured--name{
	font-size: 60px;
	font-weight: bold;
}
.featured--info{
	font-size: 18px;
	font-weight: bold;
	margin-top: 15px;
}

.featured--point,.featured--year,.featured--seasons{
	display: inline-block;
	margin-right: 15px;
}

.featured--point{
	color: #46d369;
}

.featured--description{
	margin-top: 15px;
	font-size: 20px;
	font-weight: bold;
	color: #888;
	max-width: 40%;
}

.featured--buttons{
	margin-top: 15px;
	font-size: 20px;
	font-weight: bold;
}

.featured--watch,.featured--add{
	padding: 15px 25px;
	display: inline-block;
	text-decoration: none;
	border-radius: 5px;
	margin-right: 15px;
	transition: all .3s;
}


.featured--buttons .featured--watch{
	color: #000;
	background-color: #fff;
}

.featured--buttons .featured--add{
	color: #fff;
	background-color: #999;
}

.featured--watch:hover, .featured--add:hover{
	opacity: 0.8;

}

.featured--genres{
	margin-top: 15px;
	font-size: 18px;
	color: #999;
}

.featured--created{
	display: flex;
	width: 100%;
	position: relative;
	/*top: 100px;*/
	justify-content: center;
	align-items: center;
	font-size: 20px;
	font-weight: bold;
}

.featured--created-items{
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	margin: 0px 100px;
}
.featured--created-items > p{
	margin-top: 5px;
}
header{
	position: fixed;
	z-index: 999;
	top: 0;
	left: 0;
	right: 0;
	height: 70px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 30px;
	background: linear-gradient(180deg,#111,#111,#111111aa,transparent,transparent);
	transition: .5s background ease;
}


header.black{
	background: #141414;
	transition: .5s background ease;


}

.header--logo{
	height: 25px;
}

.header--logo img{
	height: 100%;
}


.header--user{
	height: 35px;

}

.header--user img{
	height: 100%;
	border-radius: 3px;
}

